.button {
  font-size: 18px;
  line-height: 18px;
  min-width: 280px;
  font-weight: bold;
  padding: 15px;
  text-align: center;
  &__download {
    border: 3px solid #fff;
    border-radius: 30px;
    background: none;
    color: #fff;
  }
}
@media (min-width: $tablet-w) {
  .button {
    width: 300px;
  }
}